@media print {
  /* Hide these things */
  .secondary-col,
  .site-footer,
  .nav-main {
    display: none !important;
  }
  @page {
    margin: 0.5cm;
  }
  /* Use black ink for type */
  body {
    color: black;
  }
  img {
    max-width: 500px;
  }
  p a {
    word-wrap: break-word;
  }
  a {
    color: black;
  }
  .site-header {
    background: #5fb8dd;
  }
  .primary-col {
    width: 100% !important;
  }
  .primary-col img {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    border: 0;
  }
  .post-content a:after {
    content: " (" attr(href) ")";
